@import '/styles/base.scss';

.c-home-product-slider {
  &:not(.c-home-product-slider-straight) {
    transform: translateY(-264px);
    margin-bottom: t-to-rem(-210);
  }
  &.c-home-product-slider-straight {
    @include t-v-gutters(true);
    padding-top: 0!important;
  }
  @include t-gutters;

  &__products{
    display: flex;
    padding-top: t-to-rem(40);
    @include t-media-query($s-xsmall-up) {
      &.keen-slider {
        overflow: visible;
      }
    }
    &:not(.keen-slider){
      justify-content: center;
      gap: 4%;
    }
  }

  &__product{
    padding: t-to-rem(32) t-to-rem(8);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    z-index: 0;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 auto;
    border: 1px solid rgba($s-color--background-secondary, 0.2);
    background-color: $s-color--background-tertiary;
    transform: scale(1);
    transition: all $s-anim--time-slow $s-anim--ease-out;

    .c-home-product-slider-levels .keen-slider__slide.is-active &,
    .c-home-product-slider-levels .is-middle-product:not(.keen-slider__slide) &{
      margin-top: t-to-rem(0);
      transform: scale(1);
    }

    .keen-slider__slide.is-active &,
    .is-middle-product:not(.keen-slider__slide) &{
      position: relative;
      background-color: $s-color--background;

      &-title{
        color: $s-color--font-1;
      }
    }

    &-hover-background-container{
      display: none;
    }

    &-wrapper{
      position: relative;
      overflow: visible !important;
      flex-shrink: 0;
      display: flex;
      align-items: flex-start;
    }

    &-above_title {
      margin-bottom: t-to-rem(20);
      p {
        color: $s-color--font-1;
        font-weight: 700;
        font-size: t-to-rem(20);
        line-height: t-to-rem(24);
        text-align: center;
      }
    }

    &-title{
      color: $s-color--font-1;

      font-weight: 700;
      font-size: t-to-rem(40);
      line-height: t-to-rem(48);
      text-align: center;
      margin-bottom: t-to-rem(20);
      margin-top: auto;
    }

    & .okeReviews > div {
      display: flex;
      flex-direction: row;
      align-items: center;
    }

    & .oke-sr-count-number,
    & .oke-sr-rating {
      font-weight: normal;
      font-size: t-to-rem(22);
      line-height: t-to-rem(26);
    }

    & .oke-sr-rating {
      display: inline;
      margin-right: 8px;
    }

    & .oke-sr-count-number {
      color: #779196;
    }

    & .oke-sr-count-number:before {
      content: "(";
      display: inline;
    }
    & .oke-sr-count-number:after {
      content: ")";
      display: inline;
    }
    & .oke-sr-label-text {
      display: none;
    }

    &-image {
      height: 100%;
      transform: scale(0.98);
      position: absolute;
      width: auto;
      transition: transform $s-anim--time-med;

      &-container{
        margin: t-to-rem(16) auto t-to-rem(26);
        position: relative;
        padding-bottom: 79%;
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }

    &-buttons {
      display: flex;
      flex-direction: column;
    }

    &-cta {
      @extend %s-btn--primary;
      margin-bottom: t-to-rem(20);
      min-width: t-to-rem(112);
      z-index: 100;
      padding-left: t-to-rem(54);
      padding-right: t-to-rem(54);

      & > span {
        font-weight: 700;
        font-size: t-to-rem(25);
        line-height: t-to-rem(30);
      }
    }
    &-atc {
      min-width: t-to-rem(112);
      z-index: 100;
      .c-home-product-slider__product__add-cart {
        display: flex !important;
        flex-direction: row;
        align-items: center !important;
        background-color: transparent!important;
        flex-wrap: nowrap;
        column-gap: 8px;

        & > * {
          font-weight: 700;
          font-size: t-to-rem(25);
          line-height: t-to-rem(30);
          color: $s-color--font-1;
          &.is-divider {
            font-size: t-to-rem(30);
            margin-top: -8px;
          }
        }
      }

    }
    &-text{      
      color: $s-color--font-1;
    }
  }

  &__dots{
    display: flex;
    justify-content: center;
    margin-top: t-to-rem(48);
  }

  &__dot{
    @include t-width-and-height(9);
    margin-right: t-to-rem(24);
    cursor: pointer;
    background-color: $s-color--font-1;
    opacity: 0.2;
    border-radius: 100%;
    &.is-active{
      opacity: 1;
    }
    &:last-of-type{
      margin-right: 0;
    }
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-small-up) {
    &:not(.c-home-product-slider-straight) {
      transform: translateY(-171px);
      margin-bottom: -9.6875rem;
    }
  
    &__product{
      width: t-to-rem(350);
      padding: 12% 9%;
      &-title{
        margin-bottom: t-to-rem(18);
      }
      
      &-text{
        font-size: t-to-rem(14);
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &:not(.c-home-product-slider-straight) {
      transform: translateY(t-to-rem(-270));
      margin-bottom: -16.8125rem;
    }

    &__dots{
      display: none;
    }
    
    &__product{
      padding: t-to-rem(32) t-to-rem(8);

      .c-home-product-slider-levels .keen-slider__slide.is-active &,
      .c-home-product-slider-levels .is-middle-product:not(.keen-slider__slide) &{
        margin-top: t-to-rem(-50); 
      }
      
      &-hover-background{
        &-container{
          opacity: 0;   
          @include t-absolute-center-x-y;
          top: 44%;
          width: 60.9%;
          z-index: -1;
          display: flex;
          transition: opacity .5s;

          .keen-slider__slide.is-active &,
          .is-middle-product:not(.keen-slider__slide) &{
            svg path{
              stroke: $s-color--font-2;
            }
          }
        }
      }

      &:hover {
        .c-home-product-slider{
          &__product{
            &-hover-background-container{
              opacity: 1;
            }
            &-image{              
              transition: transform .5s;
              transform: scale(1);
            }
          }
        }
      }

      &-image {
        transform: scale(.9);
        transition: transform .2s;
      }

      &-title{
        letter-spacing: -0.01em;
        margin-bottom: 2%;
      }
      &-cta{      
        margin-bottom: t-to-rem(20);
      }
      &-text{
        width: 90%;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__product{
      padding: 12% 9%;

      &-text{
        font-size: t-to-rem(18);
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__product{
      width: t-to-rem(509);
    }
  }
}
