/*
 *  Button Base
 *    Base styles that other buttons inherit.. Sets up the defaults.
 */

%s-btn--base {
  cursor: pointer;
  display: inline-block;
  font-size: $s-font--type-2-size;
  letter-spacing: $s-font--type-2-letter;
  line-height: $s-font--type-2-line;
  color: $s-color--black;
  font-family: $s-font--family-1;
  font-weight: $s-font--weight-bold;

  @include t-media-query($s-medium-up) {
    font-size: $s-font--type-2-size-laptop;
  }
  @include t-media-query($s-xlarge-up) {
    font-size: $s-font--type-2-size-desktop;
  }
}


%s-block-btn {
  @extend %s-btn--base;
  padding: t-to-rem(10) t-to-rem(37);
  align-items: center;

  @include t-media-query($s-medium-up) {
    padding: t-to-rem(14) t-to-rem(28);
  }
  @include t-media-query($s-xlarge-up) {
    padding: t-to-rem(15) t-to-rem(36);
  }
}


%s-btn--primary {
  @extend %s-block-btn;
  color: $s-color--white;
  @include t-anim--background-swipe($s-color--background-octonary);
  background-color: $s-color--font-1;
  @include t-media-query($s-medium-up) {
    padding: t-to-rem(13) t-to-rem(32);
  }
  @include t-media-query($s-xlarge-up) {
    padding: t-to-rem(15) t-to-rem(34);
  }
}


%s-btn--secondary {
  @extend %s-block-btn;
  @include t-anim--background-swipe($s-color--background-quinary);
  background-color: $s-color--background-octonary;
  color: $s-color--font-1;
  padding: t-to-rem(10) t-to-rem(37);

  @include t-media-query($s-medium-up) {
    padding: t-to-rem(13) t-to-rem(32);
    &:hover {
      filter: brightness(1.2);
    }
  }
  @include t-media-query($s-xlarge-up) {
    padding: t-to-rem(15) t-to-rem(34);
  }
}


%s-btn--tertiary {
  @extend %s-block-btn;
  @include t-anim--background-swipe($s-color--background-quaternary);
  background-color: $s-color--white;
  padding: t-to-rem(10) t-to-rem(21);
  text-align: center;
  color: $s-color--font-1;

  @include t-media-query($s-medium-up) {
    padding: t-to-rem(13) t-to-rem(32);
  }
  @include t-media-query($s-xlarge-up) {
    padding: t-to-rem(15) t-to-rem(45);
  }
}

%s-btn--quaternary {
  @extend %s-btn--tertiary;
  border: 1px solid $s-color--blue-green;
}



%s-btn--disabled {
  background: $s-color--disabled-grey;
  cursor: not-allowed;
  @include t-media-query($s-medium-up) {
    &:hover {
      background-color: $s-color--disabled-grey;
    }
  }
}